import React, { useRef, useState } from "react";
import { Button, Card, CardBody, Dropdown } from "react-bootstrap";
import { SplideSlide } from "@splidejs/react-splide";
import SlideSection from "./common/slideSection";
import NetworkIcon from "../../Analytics/layout/common/networkIcon";
import { Link, useNavigate } from "react-router-dom";
import { getWidgetWallData } from "../../../../../actions/wallDataAction";
import { shallowEqual, useDispatch } from "react-redux";
import { toggleAddFeedsSection_D } from "../../../../../actions/showDisplayModalAction";
import { openCreateNewWallPopup } from "../../../../../actions/wallsAction";
import DropdownComponet from "../../Walls/Component/modal/Dropdown";
import WallsSkeleton from "./skeleton/wallsSkeleton";
import HoverVideoPlayer from "react-hover-video-player";
import { baseUrl, kFormatter } from "../../../../../utils";
import CreateWall from "./common/createWall";
import { useSelector } from "react-redux";
import PercentDifference from "../../Analytics/layout/common/percentDifference";
import { getWallFeeds } from "../../../../../actions/analyticsAction";
import { FontAwesomeIcon } from "../../../../CommonComponents/AssetsHelper";

const CreatedWalls = (props) => {
  const { allWalllist, loader } = props;
  const { userData } = useSelector(
    (state) => ({
      wallReducer: state.wallReducer,
      userData: state.authenticated.userData,
    }),
    shallowEqual
  );

  const splideRef = useRef(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [upgrade, setUpgrade] = React.useState(false);
  const [modalData, setModalData] = React.useState({
    profanity: 0,
    modalShow: false,
    name: "",
    url: "",
    wall_id: null,
    autoUpdate: 1,
    rename_status: 0,
    callbackWall: 0,
  });

  const [activeWall, setactiveWall] = useState(0);
  const { user, UserPlan } = props.userData;

  const callbackWall = (status) => { };

  const handleCategoryClick = (startIndex) => {
    if (splideRef.current) splideRef.current.splide.go(startIndex); // Move to the specific slide
  };

  const displayWindowOpen = (wallId) => {
    dispatch(getWidgetWallData(user.ownerId, wallId));
    dispatch(getWallFeeds(user.ownerId));
    navigate(`/wall/preview/${wallId}`);
  };


  const nevigationRedirect = (activeIndex, wallId = 0, nevigation = 0, feedId = 0) => {
    if (allWalllist.length != 1 && !activeIndex) return;
    switch (nevigation) {
      case 1:
        navigate(`/moderation/index/${wallId}`);
        localStorage.setItem("wallId", wallId);
        dispatch(getWidgetWallData(user?.ownerId, wallId));
        break;

      case 2:
        const analyticPath = ![58].includes(UserPlan?.planId) ? "/analytic/" : "analytic/";
        navigate(`${analyticPath}${wallId}`);
        dispatch(getWidgetWallData(user?.ownerId, wallId));
        break;

      case 3:
        displayWindowOpen(wallId);
        break;

      case 4:
        if (feedId?.length < 1) dispatch(toggleAddFeedsSection_D(true, `/wall/index/${wallId}`, navigate));
        else dispatch(toggleAddFeedsSection_D(false, `/wall/index/${wallId}`, navigate));
        dispatch(getWidgetWallData(user?.ownerId, wallId));
        break;

      case 5:
        if (feedId?.length < 1) dispatch(toggleAddFeedsSection_D(true));
        dispatch(getWidgetWallData(user?.ownerId, wallId));
        navigate(`/wall/index/${wallId}`);
        break;

      default:
        break;
    }
  };

  const clickToUpdateState = (data) => (event) => {
    dispatch(openCreateNewWallPopup(false));
    const widgetCount =
      allWalllist && allWalllist.length ? allWalllist.length : 0;
    if (widgetCount < userData.UserRule.walls || data.rename_status == 1)
      setModalData(data);
    else setUpgrade(true);
  };

  return (
    <div className="created-walls-section mb-4">
      <Card className="p-0 card-custom card-stretch">
        {(allWalllist?.length || loader) ? (
          <>
            <Card.Header className="p-3">
              <div className="m-0 d-flex align-items-center justify-content-between card-title h5 w-100">
                <h6 className="card-label font-weight-bolder m-0 ">
                  My Walls
                </h6>
                {!loader ? (
                  <div className="">
                    <Link to={"/walls"} className="text-secondary font-size-base">
                      View All
                    </Link>
                  </div>
                ) : null}
              </div>
            </Card.Header>

            <CardBody className="p-3">
              {loader ? (
                <WallsSkeleton />
              ) : (
                <>
                  <div className="row mb-4 ">
                    <div className="col-sm-8">
                      <div className="custom-pagination">
                        {Object.keys(allWalllist).length &&
                          allWalllist.map((e, index) => (
                            <Button variant={`${activeWall == index || Object.keys(allWalllist).length == 1 ? "primary" : "outline-dark"}`} className={`me-2 mb-sm-0 mb-1 text-ellipsis text-capitalize mx-w-200 ${activeWall == index || Object.keys(allWalllist).length == 1 ? "h-38" : ""}`} key={index} onClick={() => { handleCategoryClick(index); setactiveWall(index); }}>
                              {e.name}
                            </Button>
                          ))}
                      </div>
                    </div>
                    <div className="col-sm-4">
                      <div className="custom_primary_secondary d-flex justify-content-end">
                        <CreateWall modalData={modalData} widgetWallsData={allWalllist} upgrade={upgrade} clickToUpdateState={clickToUpdateState} setUpgrade={setUpgrade} setupdateWall={""} setModalData={setModalData} isCollaboratorRole={JSON.parse(localStorage.getItem("isCollaborator"))} />
                      </div>
                    </div>
                  </div>

                  {/* Splide Slider */}

                  <SlideSection
                    section={1}
                    totalRecord={allWalllist.length}
                    splideRef={splideRef}
                    setactiveWall={setactiveWall}>
                    {allWalllist.map((e, index) => {
                      const activewallIndex = activeWall === index
                      return (
                        <SplideSlide data-feedList={e.feedList} data-wallId={e.id} className={`splide__slide_walls`} key={index}>
                          <div key={activeWall + index} className={`created-walls-card h-100 splide__slide-${index}`}>
                            <div className={`created-walls-card-inner h-100 p-3 ${(activeWall === index || allWalllist.length == 1) ? 'overlay-hide' : ''}`}>
                              <div className="row">
                                <div className="col-md-6 nevigation-wall">
                                  <div className="wall_cont_wrap innter_wall_cont_wrap_ cursor-pointer created-walls-card-img p-0">
                                    <Button variant="" onClick={() => nevigationRedirect(activewallIndex, e.id, 4, e.feedList)}
                                      className="card card-custom overlay shadow-none overflow-hidden rounded-0">
                                      {(activeWall === index || allWalllist.length == 1) ? (
                                        <>
                                          <video className="tb_post_modal_video w-100 h-100 mx-h-470" muted autoPlay={true}
                                            data-type="video" data-load={0} loop playsinline="playsinline" webkit-playsinline
                                            src={`${baseUrl(`theme-media/theme-video/${e.signageTheme}.mp4?v=45445`)}`}
                                            style={{ paddingTop: "-10px",}}
                                          ></video>  <img role="img" src={`${baseUrl(`theme-media/theme-thumb/${e.signageTheme}.png`
                                            )}`} height={260} width={"100%"}/>
                                        </>
                                      ) : (
                                        <>
                                          <HoverVideoPlayer className="position-absolute h-100"
                                            videoSrc={`${baseUrl(`theme-media/theme-video/${e.signageTheme}.mp4?v=454`)}`} width={391}
                                            height={300} style={{width: "100%", paddingTop: "-10px"}}
                                          /> <img role="img" src={`${baseUrl(`theme-media/theme-thumb/${e.signageTheme}.png`)}`} height={260} width={"100%"}/>
                                        </>
                                      )}
                                      <div className="overlay-layer">
                                        <FontAwesomeIcon type={'solid'} icon={'circle-play'} iconClass={'fs-1 rounded-circle bg-white lh-30px'} />
                                      </div>
                                    </Button>
                                  </div>
                                </div>
                                <div className="col-md-6">
                                  <div className="mt-3 d-flex flex-column justify-content-between h-100">
                                    <div className="wall_section-2">
                                      <div className="d-flex justify-content-between mb-3">
                                        <div className="wall-section-title me-3">
                                          <Button type="submit" variant="" className="p-0 border-0" onClick={() => (activeWall === index || allWalllist.length == 1) ? nevigationRedirect(activewallIndex, e.id, 4, e.feedList) : {}}>
                                            <div className="text-start">
                                              <h5 className="mb-2 text-capitalize nevigation-wall">
                                                {e.name}
                                                {e.activeScreen ? (
                                                  <span className="status_active"></span>
                                                ) : null}
                                              </h5>
                                              <p className="text-secondary text-capitalize m-0">
                                                {`${e.themeName} : ${e.themeLayout}`}
                                              </p>
                                            </div>
                                          </Button>
                                        </div>
                                        <div className="">
                                          <p className="text-secondary font-weight-bold font-size-h6 m-0 d-flex text-nowrap">
                                            Posts: {kFormatter(Math.round(e.totalPostCount))}
                                            {e.difference ? <PercentDifference difference={e.difference} flag={e.flagColor} /> : null}
                                          </p>
                                        </div>
                                      </div>
                                      {e.feedList &&
                                        Object.keys(e.feedList).length ? (
                                        <div className="created-walls-network mb-4">
                                          <ul className="p-0 m-0">
                                            {e.feedList.map((fdata, key) => {
                                              return (
                                                <li className="d-flex align-items-center mb-3">
                                                  <NetworkIcon isDashboard={"me-2 text-center text-white"} icon={fdata.socialIcon} />
                                                  {fdata.name.replace(/<i[^>]*>(.*?)<\/i>/g, '')}
                                                </li>
                                              );
                                            })}
                                          </ul>
                                        </div>
                                      ) : null}
                                    </div>

                                    <div className="d-flex align-items-center justify-content-end mb-3">
                                      <div className="created-walls-report d-flex align-items-center flex-wrap">
                                        <Button variant="light" onClick={() => nevigationRedirect(activewallIndex, e.id, 1)}
                                        className="f-center btn-hover-primary me-2 mb-md-0 mb-2 border-0 text-dark">
                                          <FontAwesomeIcon type={'regular'} icon={'check-to-slot'} iconClass={'me-2 fs-5'} />{" "} Moderate
                                        </Button>
                                        <Button variant="light" onClick={() =>nevigationRedirect(activewallIndex, e.id, 2)}
                                          className="f-center btn-hover-primary text-dark me-2 mb-md-0 mb-2 border-0">
                                          <FontAwesomeIcon type={'light'} icon={'chart-simple'} iconClass={'me-2 fs-5'} />{" "} Analytics
                                        </Button>
                                        <Button variant="light" onClick={() => nevigationRedirect(activewallIndex, e.id, 3)} className="f-center btn-hover-primary text-dark me-2 mb-md-0 mb-2 border-0">
                                          <FontAwesomeIcon type={'regular'} icon={'display'} iconClass={'me-2 fs-5'} />{" "} Display
                                          </Button>

                                          <Button variant="light" onClick={() => (activeWall === index || allWalllist.length == 1) ? nevigationRedirect(activewallIndex, e.id, 5, e.feedList) : {}} className="f-center btn-hover-primary text-dark me-2 mb-md-0 mb-2 border-0">
                                            <FontAwesomeIcon type={'regular'} icon={'pen-to-square'} iconClass={'me-2 fs-5'} />{" "} Edit
                                          </Button>
                                      </div>

                                      <div className="card-toolbar ms-2">
                                        <Dropdown onClick={() => nevigationRedirect(activewallIndex)} align={"end"}>
                                          <Dropdown.Toggle variant="link" className="btn-after-none">
                                            <FontAwesomeIcon type={'regular'} icon={'ellipsis-vertical'} iconClass={'fs-5'} />
                                          </Dropdown.Toggle>
                                          <Dropdown.Menu
                                            style={{ width: 175 }}
                                          >
                                            <DropdownComponet
                                              callbackWall={callbackWall}
                                              props={{
                                                wallsData: e,
                                                clickToUpdateState:
                                                  clickToUpdateState,
                                              }}
                                              clickToUpdateState={
                                                clickToUpdateState
                                              }
                                              ownerId={user.ownerId}
                                              userData={props.userData}
                                            />
                                          </Dropdown.Menu>
                                        </Dropdown>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </SplideSlide>
                      );
                    })}
                  </SlideSection>
                </>
              )}
            </CardBody>
          </>
        ) : (
          <div className="col-12">
            <div className="d-flex justify-content-center w-100">
              <Button variant=""
                onClick={() =>
                  clickToUpdateState({
                    modalShow: true,
                    wall_id: null,
                    autoUpdate: 1,
                    callbackWall: 1,
                  })
                }
                className="d-flex flex-wrap flex-column align-content-center add_card_button create-new-wall cursor-pointer px-5 w-100">
                <CreateWall
                  firstWall={1}
                  widgetWallsData={allWalllist}
                  modalData={modalData}
                  upgrade={upgrade}
                  clickToUpdateState={clickToUpdateState}
                  setUpgrade={setUpgrade}
                  setupdateWall={""}
                  setModalData={setModalData}
                  isCollaboratorRole={JSON.parse(
                    localStorage.getItem("isCollaborator")
                  )}
                />
              </Button>
            </div>
          </div>
        )}
      </Card>
    </div>
  );
};

export default React.memo(CreatedWalls);